import React from "react"

import Layout from "../../elements/layout"
import Header from "../../elements/header/header"
import SEO from "../../elements/seo"
import HeaderImage from "../../elements/headerimg/headerimage"
import { i18n } from "../../i18n/language"
import Button from "../../elements/button/button"
import Container from "../../elements/container/container"
import Seperator from "../../elements/seperator/seperator"
import LaptopBox from "../../elements/laptopbox"
import Video from "../../elements/video/video"
import { navigate } from "gatsby"

const CollaborationPage = ({ pageContext }) => (
  <Layout>

    <SEO title={i18n.collaboration.meta.title}
         description={i18n.collaboration.meta.description}/>

    <Header title={i18n.collaboration.title}
            subtitle={i18n.collaboration.subtitle}>


      <Container>
        <Button onClick={() => navigate('/collaboration/file/?workspace=' + new URLSearchParams(window.location.search).get("workspace"))} tagName={"button"}>{i18n.collaboration.showFile}</Button>
        <a href={"#video"} className={"mt-15"}>{i18n.collaboration.explain}</a>
      </Container>

      <Seperator id={"video"}/>

      <HeaderImage>
        <Video project={'mietwise'}/>
      </HeaderImage>
    </Header>

    <Seperator/>

  </Layout>
)

export default CollaborationPage
